@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,400;0,500;0,700;1,200&display=swap');

/* font-family: 'Montserrat', sans-serif; */

#grid-container{
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 500px 100px;
    width: 100%;
    height: 100vh;
    font-family: 'Montserrat', sans-serif;
    background-color: #fdfdfd;
    color: rgb(100, 100, 100);
    grid-row-gap: 20px;
}

#grid-item1 {
    /* background-image: url("https://images.unsplash.com/photo-1533814241121-a93e5cd7399b?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1350&q=80" ); */
    background-size: 100% 100%;
    background-repeat: no-repeat;
}

#grid-item1 p  {
    margin-top: 100px ;
    letter-spacing: 0.2em;
}
/** Hero Buttons **/
#grid-item1 button  {
    letter-spacing: 0.2em;
    color: #fff;
    background-color: rgba(128, 128, 128, 0.363);
    border: none;
    margin: 0 30px;
    padding: 10px 20px;
    border-radius: 5px;
}

#grid-item1 button:hover  {
    color: #ddd;
}

#grid-item1 button:focus  {
    outline:0;
}

/**  **/
.sectiontitle {
    font-family: sans-serif;
    font-size: 30px;
    line-height: 41px;
    font-weight: 700;
    letter-spacing: 0.05em;
    color: black;
    /* margin-top: 10px;
    margin-bottom: 0; */
}

.herotitle {
    margin-bottom: 20px;
    font-family: sans-serif;
    font-size: 30px;
    line-height: 41px;
    font-weight: 700;
    letter-spacing: 0.05em;
    /* color: rgb(102, 98, 98); */
    color: black;
    
    /* margin-top: 10px;
    margin-bottom: 0; */
}

/** How it works **/
#grid-item2 {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr;
    grid-gap: 5px;


}

.subImage {
    height: 180px;
    width: 220px;
}

.subTitle {
    font-weight: 500;
    letter-spacing: 0.1em;
}

.subText {
    letter-spacing: 0.05em;
}
/** Why it works **/

#sub-Text3 {
    margin-left: 40px;
    margin-right: 40px;
    text-align: left;
    text-justify: inter-word;
}

/** Testimonials / Reviews **/

#grid-item4 {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr;
    grid-gap: 5px;

}

.stars {
    width: 40%;
}
