@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,400;0,500;0,700;1,200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* font-family: 'Montserrat', sans-serif; */

#grid-container{
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 500px 100px;
    width: 100%;
    height: 100vh;
    font-family: 'Montserrat', sans-serif;
    background-color: #fdfdfd;
    color: rgb(100, 100, 100);
    grid-row-gap: 20px;
}

#grid-item1 {
    /* background-image: url("https://images.unsplash.com/photo-1533814241121-a93e5cd7399b?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1350&q=80" ); */
    background-size: 100% 100%;
    background-repeat: no-repeat;
}

#grid-item1 p  {
    margin-top: 100px ;
    letter-spacing: 0.2em;
}
/** Hero Buttons **/
#grid-item1 button  {
    letter-spacing: 0.2em;
    color: #fff;
    background-color: rgba(128, 128, 128, 0.363);
    border: none;
    margin: 0 30px;
    padding: 10px 20px;
    border-radius: 5px;
}

#grid-item1 button:hover  {
    color: #ddd;
}

#grid-item1 button:focus  {
    outline:0;
}

