/** How it works **/
#grid-item2 {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr;
    grid-gap: 5px;


}

.subImage {
    height: 180px;
    width: 220px;
}

.subTitle {
    font-weight: 500;
    letter-spacing: 0.1em;
}

.subText {
    letter-spacing: 0.05em;
}
/** Why it works **/

#sub-Text3 {
    margin-left: 40px;
    margin-right: 40px;
    text-align: left;
    text-justify: inter-word;
}

/** Testimonials / Reviews **/

#grid-item4 {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr;
    grid-gap: 5px;

}

.stars {
    width: 40%;
}
